import { Component, Input, OnInit } from '@angular/core';
import { SeoService } from 'src/app/core/services/header/seo.service';
import { OrderService } from 'src/app/core/services/order.service';

@Component({
  selector: 'app-test-drive',
  templateUrl: './test-drive.component.html',
  styleUrls: ['./test-drive.component.scss'],
})
export class TestDriveLandingPageComponent implements OnInit {

  @Input() selectIsOpen: boolean = false;
  @Input() submitted: boolean = false;

  public filialId: string = '483';
  public buttonLabelText: string = 'Enviar';

  constructor(
    private orderService: OrderService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.seoService.createLinkForCanonicalURL();
  }

  toggleSelect() {
    const options = Array.from(document.querySelectorAll('[data-filial-id]'));

    if (!this.selectIsOpen) {
      options.forEach(option => option.classList.add('active'));
    }

    this.selectIsOpen = !this.selectIsOpen;
  }

  toggleOption(event) {
    const option = event.currentTarget;
    const options = Array.from(document.querySelectorAll('[data-filial-id]'));

    options.forEach(option => option.classList.remove('active'));
    option.classList.add('active');

    this.filialId = option.dataset.filialId;
    this.toggleSelect();
  }

  formatNumber(event) {
    const formattedNumber = event.target.value
      .toString()
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '($1) $2')
      .replace(/(\d{4})(\d)/, '$1-$2')
      .replace(/(\d{4})-(\d)(\d{4})/, '$1$2-$3')
      .replace(/(-\d{4})\d+?$/, '$1');

    event.target.value = formattedNumber;
  }

  submit(event){
    event.preventDefault();

    const form = document.forms[0];
    const date = (<HTMLInputElement>form.querySelector('[name="date"]:checked')).value;
    const name = (<HTMLInputElement>form.querySelector('#form-name')).value;
    const phone = (<HTMLInputElement>form.querySelector('#form-phone')).value;
    const email = (<HTMLInputElement>form.querySelector('#form-email')).value;
    const vehicle = (<HTMLInputElement>form.querySelector('#form-vehicle')).value;
    const news = (<HTMLInputElement>form.querySelector('#form-news')).value;
    const terms = (<HTMLInputElement>form.querySelector('#form-terms')).value;

    const data = {
      data: date,
      nome: name,
      telefone: phone,
      email: email,
      veiculo: vehicle,
      aceiteComunicados: news,
      leuTermos: terms,
      filialId: this.filialId
    }

    if (!this.validateForm()) return;

    this.buttonLabelText = 'Enviando...';

    this.orderService.createLandingPage(data).subscribe(() => {
      this.buttonLabelText = 'Enviar';

      this.showThanks();
      form.reset();
    }, error => {
      this.buttonLabelText = 'Enviar';

      console.error(error);
    });
  }

  validateForm() {
    const requiredFields = Array.from(<NodeListOf<HTMLInputElement>>document.querySelectorAll('[required]'));
    const requiredFieldUnfilled = <HTMLInputElement>requiredFields.find(field => field.value == '' || field.type === 'checkbox' && field.checked === false);

    if (requiredFieldUnfilled) {
      alert(`Campo ${requiredFieldUnfilled.dataset.field} obrigatório`);

      return false;
    }

    return true;
  }

  showThanks() {
    const formContainer = <HTMLElement>document.querySelector('.registration__form');
    const formPosition = formContainer.offsetTop;

    this.submitted = true;

    window.scrollTo(0, formPosition);
  }
}
